.line {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.line ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.line ul li {
  list-style-type: none;
  position: relative;
  width: 6px;
  margin: 0 auto;
  padding-top: 50px;

  animation-name: visible;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  background-color: #006e51;
}
@keyframes animate {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
    transform: translateX(30px);
  }
}

.line ul li::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: inherit;
}
.line ul li:hover:after {
  background-color: #8f2033;
}

.line ul li div {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: relative;
  bottom: 0;
  width: 400px;
  height: 170px;
  padding: 15px;
  background: #eadb8b;
  border-radius: 4px;
  transition: all 0.7s ease-in-out;

  background-color: #f8f5de;
  background-image: linear-gradient(
    to right,
    rgba(255, 210, 0, 0.4),
    rgba(200, 160, 0, 0.1) 11%,
    rgba(0, 0, 0, 0) 35%,
    rgba(200, 160, 0, 0.1) 65%
  );
  box-shadow: inset 0 0 75px rgba(255, 210, 0, 0.3),
    inset 0 0 20px rgba(255, 210, 0, 0.4), inset 0 0 30px rgba(220, 120, 0, 0.8);
  color: rgba(0, 0, 0, 0.8);
  letter-spacing: 0.05em;
  line-height: 1.6;

  @include for-mobile {
    width: 100px;
  }
  @include breakpoint {
    max-width: 270px;
  }
}

.line ul li div:hover {
  transform: scale(1.1);
}
.line ul li div::before {
  content: "";
  position: absolute;
  bottom: 7px;
  width: 0;
  height: 0;
  border-style: solid;
}

.line ul li:nth-child(odd) div {
  left: 45px;
  @include for-mobile {
    height: 60px;
  }
}

.line ul li:nth-child(odd) div::before {
  left: -15px;
  border-width: 8px 16px 8px 0;
  border-color: transparent #eadb8b transparent transparent;
}

.line ul li:nth-child(even) div {
  left: -439px;
  @include breakpoint {
    left: -310px;
  }
  @include for-mobile {
    left: -140px;
    height: 60px;
  }
}

.line ul li:nth-child(even) div::before {
  right: -15px;
  border-width: 8px 0 8px 16px;
  border-color: transparent transparent transparent #eadb8b;
}

.dotList {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-width: 150px;
}

.buttonPink {
  color: #006e51;
  border: none;
  background-color: transparent;
  float: right;
  font-size: 23px;
  transition: all 0.2s ease-in-out;
}

.buttonPink:hover,
.buttonPink:focus {
  color: #129471;
  text-decoration: none;
  cursor: pointer;
  transform: scale(1.1);
}

.hidden {
  @include for-mobile {
    display: none;
  }
}

.textContainer {
  @include for-mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;
  }
}
